.queryBox {
    overflow: hidden; 
    direction: rtl;
}

.queryMeta {
    float: left;
    width: 165px;
}

.queryBoxContainer {
     max-width: 100%; 
     margin-top: 3px;
}

.textInnerBox {
    white-space: nowrap; 
    direction: ltr; 
    display: inline-block;
}

.queryContainer {
    padding: 15px;
    border: 3px solid black;
    background-color: #fff;
    border-radius: 15px;
    margin-bottom: 10px;
    height: 80px;
}

.queryProgressBar {
    width: 90px;
}

.queryKey {
    text-align: center;
    font-size: 10pt;
    position: relative;
    margin-right: 10px;
    color: #666;
    top: -10px;
    left: 1px;
}

.searchIcon {
    text-align: center;
    font-size: 15pt;
    position: relative;
    top: -10px;
    left: 1px;
}

.searchMetaIcon {
    margin-right:5px;
    margin-top:6px;
    display: inline-block;
    width: 20px;
    text-align: center;
}

.cursor {
    animation:blinkingCursor 1.0s infinite;
    font-size: 16pt;
    margin-left: -2px;
    margin-right: -2px;
    font-weight: 200; 
}

@keyframes blinkingCursor{
    0% { opacity: 1; }
    49% { opacity: 1; }
    50% { opacity: 0; }
   100% { opacity: 0; }
}