.topicInfoContainer {
    padding: 15px;
    border: 3px solid black;
    background-color: #fff;
    margin: 10px;
    border-radius: 15px;
}

.topicControls {
    min-width: 360px;
    flex-basis: auto;
    flex-grow: 1;
}

.logo {
    width: 100%;
}

.queryRow {
    padding-left:10px;
    padding-right:10px;
}

.statsColumn {
    text-align: center;
}

.topicProgressRow {
    /*height: 37px;*/
    height:37px;
    vertical-align: middle;    
    padding-top: 8px;
    text-align: center;
}

.topicProgressCol {
    text-align:center;
}

.progressBar {
    width: 100%;
}

.playbackIcon {
    display: inline-block;
    text-align: center;
    font-size: 20pt;
    margin-left:10px;
    margin-right:10px;
}

.playbackSolidIcon {
    display: inline-block;
    text-align: center;
    font-size: 18pt;
    margin-left:10px;
    margin-right:10px;
}

.forwardBackSolidIcon {
    display: inline-block;
    text-align: center;
    font-size: 12pt;
    margin-left:10px;
    margin-right:10px;
}

